  justify-content: center;
  text-align: center;
  position: absolute;
  color: #4795d6;
  top: -5px;
  right: 1px;
  font-weight: bold;

}

.totalItemsContainer  .shoppingButton{
    
    display: inline-block;
    text-align: center;
    outline: none;
    border: none;
}

.shoppingButton:active{
