.containerDishesList{
  /*border: 2px solid blue;  */

}

.dishesList{
  /*height: 90%; */
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  /*border: 2px solid green; */
  padding-top: 20px;
  padding-bottom: 20px;

}

.dish{
  width: 200px;
  border-radius: 15px 0 0 15px;
  /*border: 2px solid tomato; */
  padding-bottom: 16px;
}

.dish .dishImage{
 /* border:1px solid blue; */
 position: relative; 
 margin: 0 auto;

}

.dish .dishImage .description{
  position: absolute; /* Position the background text */
  bottom: 0; /* At the bottom. Use top:0 to append it to the top */
  background: rgb(0, 0, 0); /* Fallback color */
  background: rgba(0, 0, 0, 0.5); /* Black background with 0.5 opacity */
  color: #f1f1f1; /* Grey text */
  width: 100%; /* Full width */
  font-size: 10px;
  font-weight: bold;
}

.dish .dishContent{
  border-bottom-left-radius: 15px;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.dish .dishImage img{
  width: 100%;
  height: 132px;
  border-top-left-radius: 15px;
  vertical-align: bottom;
}


.dish .dishContent .dishPrice{
  width: 30px;
  height: 30px;
  border-radius: 50px;
  /*border:1px solid red; */
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 12px;

}

.dish .dishContent .dishTitleDescription{
 /* border:1px solid green; */
  width: 100px;
  text-align: center;
}

.dish .dishContent .dishTitleDescription .dishTitle{
  font-size: 14px;
  font-family: 'Inconsolata', monospace;
  font-weight: bold;
}

.dish .dishContent .dishTitleDescription .dishDescription{
  font-size: 12px;
}

.description p{
  padding-left: 8px;
}

.dish .dishContent .dishUpDownButtons{
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 4px;
  padding-bottom: 4px;
}

.dish .dishContent .dishUpDownButtons .ordered{
  font-size: 12px;
}

.btn{
  display: inline-block;
  text-decoration: none;
  color: #FFF;
  width: 40px;
  height: 40px;
  line-height: 40px;
  border-radius: 50%;
  text-align: center;
  vertical-align: middle;
  overflow: hidden;
  transition: .4s;
  border: none;
  outline: none;
}

.btn:active{
  outline: none;
  border:none;
}
.btn:focus{
  outline:0;
}


.btnUpDown{
  display: flex;
  justify-content: center;
  align-items: center;
  background: #fff;
  color: #000;
  width: 18px;
  height: 18px;
  border-radius: 50%;
  outline: none;
  border:none;
}

.btnUpDown:active{
  outline: none;
  border:none;
}
.btnUpDown:focus{
  outline:0;
}

